<template>

<div class="logoH">
	<div class="shijian"><div class="RL">{{time.year}} 年 {{time.month}} 月 {{time.hao}} 日</div><div class="TE">{{time.hours}}:{{time.minutes}}:{{time.seconds}} 星期 {{time.day}}</div></div>
	
	<div class="logo"></div>
	<p>lyw'blog</p>

</div>
</template>
<script type="text/javascript">
	
export default{
	name:'blog-logo',
	  data(){
  	return{
  		timer:null,
  		time:{
  			minutes:'',
  			seconds:'',
  			hao:'',
  			day:'',
  			month:'',
  			year:'',
  			hours:''
  		}
  	}  
  },
  	methods:{
  		    data1:function (){
  			var T = new Date();
  			this.time.minutes = T.getMinutes();
  			this.time.seconds = T.getSeconds();
  			this.time.hao = T.getDate();
  			this.time.day = T.getDay();
  			this.time.month = T.getMonth()+1;
  			this.time.year = T.getFullYear();
  			this.time.hours = T.getHours();
  			// console.log(this.time.minutes);
  		}
  	},
  	created:function(){
  		setInterval(this.data1,1000);
  	}
}

</script>


<style scoped>
	.logoH{
		background:skyblue;
	    padding:10px 0;
        margin-bottom:40px;
		 box-shadow: 2px 2px 2px #123456;

	}
	.logo{
		 box-shadow: 2px 2px 2px #123456;
		border-radius:50%;
		margin:0 auto;
		width:120px;
		height:120px;
		background-image: url(../assets/zln.jpg);
		background-size: contain;
}
.logoH p{
	font-size:25px;
	text-align:center;
}
.shijian{
	margin-bottom:5px;
	display:flex;
	justify-content: space-around;   

}
.shijian div{
    box-shadow: 1px 1px 1px black;
	border-radius:5px;
	padding:5px;
	background:white;
	text-align: center;
}

</style>


