<template>
	<div id="single-blog">
		<h1>{{blog.title}}</h1>
		<article>{{blog.content}}</article>
		<p>作者：{{blog.author}}</p>
		<p>分类：</p>
		<ul>
			<li v-for="category in blog.categorices">{{category}}</li>
		</ul>
		<div id="edit"><button @click='deleteSingleBlog()'>删除</button>
		<router-link class="edit" :to="`/edit/${id}`">编辑</router-link></div>
	</div>
</template>


<script>
	export default{
		name:"single-blog",
		data(){
			return{
				id:this.$route.params.id,
				blog:{}
			}
		},
		created(){
			this.$http.get(`https://vuedemo-4ba5a.firebaseio.com/posts/${this.id}.json`)
			.then((data)=>{
				console.log(data);
				return data.json();
			})
			.then((data)=>{
				this.blog = data;
			})
		},
		methods:{
			deleteSingleBlog(){
				this.$http.delete(`https://vuedemo-4ba5a.firebaseio.com/posts/${this.id}.json`)
				.then(response =>{
					this.$router.push({path:'/'})
				})
			}
		}
	}

</script>
<style>
#single-blog{
	max-width:960px;
	margin: 0 auto;
	padding:20px;
	background:#eee;
	border:1px dotted #aaa;
}
button{
  display:block;
  margin:20px 0;
  background:crimson;
  color:#fff;
  border:0;
  padding:14px;
  border-radius:4px;
  font-size:18px;
  sursor:pointer;
}
button:hover{
  background:red;
}
#edit{
	width:200px;
	display:flex;
	justify-content:center;
	    align-items: center;


}
.edit{
height:24px;
	width:36px;
	display:block;
  margin-left:10px;
  background:crimson;
  color:#fff;
  border:0;
  padding:14px;
  border-radius:4px;
  font-size:18px;

}
a:hover{
	background:red;
}
</style>


