<template>
  <div id="add-blog">
    <h2>编辑博客</h2>
    <form v-if="!submmited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" name="title">
      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>
      <div id="checkboxes">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categorices" name="">
        <label>Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categorices" name="">
        <label>React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categorices" name="">
        <label>Angular4</label>
        <input type="checkbox" value="Angular4" v-model="blog.categorices" name="">
      </div>
      <label>作者：</label>
      <input v-model="blog.author">
        
      </input>
      <button name="" v-on:click.prevent="post">保存</button>
    </form>

<div v-if="submmited">
  <h3>博客编辑成功!</h3>
</div>

    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容：</p>
      <p>{{blog.content}}</p>
      <p>博客分类</p>
      <ul><li v-for="category in blog.categorices">{{category}}</li></ul>
      <p>作者：{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
    	id:this.$route.params.id,
      blog:{},
      submmited:false
    }
  },
  methods:{
  	fetchDate(){
  		// console.log(this.id)
  		this.$http.get(`https://vuedemo-4ba5a.firebaseio.com/posts/${this.id}.json`)
  		.then(response => {
  			this.blog=response.body;
  		})
  	},
    // post:function(){
    // fetch("/api/phpvue/blog.php",{
    //     method:"post",
    //     headers:{
    //       "Content-type":"application/json"
    //     },
    //     body:JSON.stringify(this.blog)
    // })
    //  .then((data)=>{console.log(data);this.submmited=true;})
    //    }

    post:function(){
      this.$http.put(`https://vuedemo-4ba5a.firebaseio.com/posts/${this.id}.json`,this.blog)
      .then((data)=>{console.log(data);this.submmited=true;})
    }
  },
  created(){
  	this.fetchDate();
  }
}
</script>
<style>
#add-blog*{
box-sizing:border-box;
}
#add-blog{
  margin:20px auto;
  max-width:600px;
  padding:20px;
}
label{
  display:block;
  margin:20px 0 10px;
}
input[typr='text'],textarea,select{
  display:block;
  width:100%;
  padding:8px;
}
textarea{
  height:200px;
}
#checkboxes label{
  display:inline-block;
  margin-top:0;
}
#checkboxes input{
  display:inline-block;
  margin-right:10px;
}
button{
  display:block;
  margin:20px 0;
  background:crimson;
  color:#fff;
  border:0;
  padding:14px;
  border-radius:4px;
  font-size:18px;
  sursor:pointer;
}
#preview{
  padding:10px 20px;
  border:1px dotted #ccc;
  margin:30px 0;
}
h3{
  margin-top:10px;
}
</style>



